.chat {
  display: flex;
  margin: auto;
  color: #fff;
  height: 500px;
}

::-webkit-scrollbar {
  width: 12px;
  border: none;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #27292d;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
